import React from "react"
/*This function will crossed out the content of the task if the checkbox is ticked.
    Also, this function will computed the TimeLeft function on the table, basically it compare the due date and current date*/
function TodoItem(props) {
    const completedstyles = {
        ontStyle: "italic",
        color: "#cdcdcd",
        textDecoration: "line-through"
    }

    //compare duedate with today's date
    const todayDate = new Date(new Date().getFullYear(),new Date().getMonth() , new Date().getDate())
    const taskDate = new Date(props.item.date)
    const taskDateNoTime = new Date(taskDate.getFullYear(),taskDate.getMonth() , taskDate.getDate())
    let messageOnTime;
    if(taskDateNoTime.toDateString() === todayDate.toDateString()){
        messageOnTime="Due Today"
    }else if (taskDateNoTime.toDateString() !== todayDate.toDateString()){
        if(taskDateNoTime.getMonth() === todayDate.getMonth() && taskDateNoTime.getFullYear() === taskDateNoTime.getFullYear()){
            let daysLeft = taskDateNoTime.getDate() - todayDate.getDate()
            
            if(daysLeft>0){
                if(daysLeft ===1){
                    messageOnTime= "Due tomorrow"
                }
                else if(daysLeft >1){
                    messageOnTime= "Due in " +daysLeft + " day(s)"
                }
                
            }else if(daysLeft<0){
                messageOnTime= "Overdue"
            }
        }else if(taskDateNoTime.getFullYear() === taskDateNoTime.getFullYear() && taskDateNoTime.getMonth() > todayDate.getMonth() ){
            let monthsLeft = taskDateNoTime.getMonth() - todayDate.getMonth()
            let daysLeft = taskDateNoTime.getDate() - todayDate.getDate()
            if(monthsLeft>0){
                if(monthsLeft ===1){
                    if(daysLeft >0){
                        if(Math.abs(daysLeft)>10){
                            messageOnTime = "Due next month"
                        }
                        if(daysLeft ===1){
                            messageOnTime = "Due tomorrow"
                        }else{
                            messageOnTime = "Due in " + daysLeft + "day(s)"
                        }
                        
                    }
                    
                }else if(monthsLeft >1){
                    messageOnTime= "Due in " + monthsLeft + " month(s)" 

                }
                
            }
        }else{
            messageOnTime= "Overdue"
        }

    }

    return (
            <tr>
                <td><input
                        type="checkbox"
                        checked={props.item.completed}
                        onChange={() => props.handleChange(props.item.id)}
                    /></td>
                <td style={props.item.completed === true ? completedstyles : null}>{props.item.text}</td>
                <td style={props.item.completed === true ? completedstyles : null}>{props.item.timeRequired}</td>
                <td style={props.item.completed === true ? completedstyles : null}>{taskDateNoTime.toDateString()}</td>
                <td style={props.item.completed === true ? completedstyles : null}>{messageOnTime}</td>
                <td style={props.item.completed === true ? completedstyles : null}>{props.item.priority}</td>
                <td><button type="button" onClick={() => props.Deletion(props.item.id)}>Delete</button></td>
            </tr>
    )
}

export default TodoItem